import React, {createContext, use, useContext, useEffect, useState} from "react";
import axiosInstance from "../lib/axios";
import { useRouter } from "next/navigation";
import { COMPILER_INDEXES } from "next/dist/shared/lib/constants";



const GlobalContext = createContext();


export const GlobalContextProvider =({children})   => {

    const router = useRouter();

    const [isAuthenticated, setIsAuthenticated] = useState(false);   
    const [auth0User, setAuth0User] = useState(null);
    const [userProfile, setUserProfile] =useState({});
    const [loading, setLoading] = useState(false);


    // input state
    const [jobTitle, setJobTitle] = useState("");
    const [jobDescription, setJobDescription] = useState("");
    const [salary, setSalary] = useState(0);
    const[activeEmploymentTypes, setActiveEmploymentTypes] = useState([]);
    const [salaryRange, setSalaryRange] = useState([0,0]);
    const [salaryType, setSalaryType] = useState("Year");
    const [negotiable, setNegotiable] = useState(false);
    const [skills, setSkills] = useState([]);
    const [location, setLocation] = useState({
        country: "",
        city: "",
        address: "",
    });
    
    useEffect(() => {
        const checkAuth = async () => {
            setLoading(true);
            try {
                const res = await axiosInstance.get("/api/v1/check-auth");
                console.log("Auth check response:", res.data);
                // Handle both old format (false) and new format (object)
                if (res.data && typeof res.data === "object") {
                    setIsAuthenticated(res.data.isAuthenticated || false);
                    setAuth0User(res.data.user || null);
                } else {
                    // Fallback for old format
                    setIsAuthenticated(false);
                    setAuth0User(null);
                }
            } catch (error) {
                console.error("Error checking authentication", error);
                // Set default values on error
                setIsAuthenticated(false);
                setAuth0User(null);
            } finally {
                setLoading(false);
            }
        };
        checkAuth();

        
    }, []);
    const [tags, setTags] = useState({
    });

    const getUserProfile = async (id) => {
        console.log("Calling getUserProfile with id:", id);
        try {
            const res = await axiosInstance.get(`/api/v1/user/${id}`);           
            //console.log("Response:", res.data);
            setUserProfile(res.data);
        } catch (error) {
            console.error("Error fetching user profile", error);
            // Set empty profile on error
            setUserProfile({});
        } 

    };
    
    // handle input changes
    const handleTitleChange = (e) => {
        setJobTitle(e.target.value.trimStart());
    }
    const handleDescriptionChange = (e) => {    
        setJobDescription(e.target.value.trimStart());
    }

    const handleSalaryChange = (e) => {
        setSalary(e.target.value);
    };


    const resetJobForm = () => {
        setJobTitle("");
        setJobDescription("");
        setSalary(0);
        setActiveEmploymentTypes([]);
        setSalaryRange([0,0]);
        setSalaryType("Year");
        setNegotiable(false);

        setSkills([]);
        setLocation({
            country: "",
            city: "",
            address: "",
        });
        setTags({
        });
    };

    



    

    
    
    useEffect(() => {
        
        if(isAuthenticated && auth0User){
            getUserProfile( auth0User.sub);
        }
        
    }, [isAuthenticated, auth0User]);
    console.log(auth0User);
    return (
        <GlobalContext.Provider value={{
            isAuthenticated,
            auth0User,
            userProfile,
            getUserProfile,
            loading,
            jobTitle,
            jobDescription,
            salary,
            salaryType,
            negotiable,
            setNegotiable,
            skills,
            setSkills,
            location,
            setLocation,
            tags,
            setTags,
            salaryRange,
            setSalaryRange,
            activeEmploymentTypes, 
            salaryRange,
            handleTitleChange,
            handleDescriptionChange,
            handleSalaryChange,
            setActiveEmploymentTypes,
            setJobDescription,
            resetJobForm,
            setSalaryType,
            
        }}>
            {children}
        </GlobalContext.Provider>
    );
};

export const useGlobalContext = () => {
    return useContext(GlobalContext);
};

export default useGlobalContext;