import React, { use,createJob,  useEffect } from "react";
import { createContext, useContext,useState } from "react";
import { useGlobalContext } from "./globalContext";
import axiosInstance from "../lib/axios";
import toast from "react-hot-toast";
import { get } from "http";





const JobsContext = createContext();


export const JobsContextProvider =({children})   => { 
    const {userProfile}=useGlobalContext();   

    const [jobs,setJobs]=useState([]);
    const [loading,setLoading]=useState(false);
    const [userJobs,setUserJobs]=useState([]);

    const getJobs = async () => {
        setLoading(true);
        try {
            const res = await axiosInstance.get("/api/v1/jobs");
            console.log("Fetched jobs:", res.data);
            setJobs(res.data || []);
        } catch (error) {
            console.error("Error fetching jobs", error);
            // Set empty array on error to prevent undefined issues
            setJobs([]);
            // Optionally show toast notification
            if (error.code === "ECONNABORTED" || error.message === "Network Error") {
                toast.error("Unable to connect to server. Please check if the server is running.");
            }
        } finally {
            setLoading(false);
        }
    };
    const createJob = async (jobData) => {
        
        try {
            const res = await axiosInstance.post("/api/v1/jobs", jobData);
            toast.success("Job created successfully");

            const newJob = res.data.Newjob || res.data;
            setJobs((prevJobs) => [ newJob, ...prevJobs]);

            //update userjobs
            if(userProfile._id){

                setUserJobs((prevUserJobs) => [newJob, ...prevUserJobs]);
                await getUserJobs(userProfile._id);
            }
            await getJobs();
            

        } catch (error) {
            console.error("Error creating job", error);
            toast.error(error.response?.data?.message || "Error creating job");
        }   
    };
    
    const getUserJobs = async (userId) => {
        setLoading(true);  
        try {
            const res = await axiosInstance.get("/api/v1/jobs/user/"+userId);
            setUserJobs(res.data || []);
        } catch (error) {
            console.error("Error fetching user jobs", error);
            setUserJobs([]);
        }finally {
            setLoading(false);
        }
    };

    const searchJobs = async (tags, location, title) => {
        setLoading(true);
        try {
            // but query string
            const querry = new URLSearchParams(); 
            if(tags) querry.append("tags", tags);
            if(location) querry.append("location", location);
            if(title) querry.append("title", title);

            // send request
            const res = await axiosInstance.get(`/api/v1/jobs/search?${querry.toString()}`);
            setJobs (res.data || []);
            

        } catch (error) {
            console.error("Error searching jobs", error);
            setJobs([]);
        } finally {
            setLoading(false);
        }
    };

    //get job by id
    const getJobById = async (jobId) => {
        setLoading (true);  
        try {
            const res = await axiosInstance.get(`/api/v1/jobs/${jobId}`);
            return res.data;
        } catch (error) {
            console.error("Error fetching job by id", error);
            return null;
        } finally {
            setLoading(false);
        }  
    };

    //like a job
    const likeJob = async (jobId) => {
        try {
            const res = await axiosInstance.put(`/api/v1/jobs/like/${jobId}`); 
            toast.success("Job liked successfully");
            getJobs();

            //update jobs state
        } catch (error) {
            console.error("Error liking job", error);
            toast.error(error.response?.data?.message || "Error liking job");
        }   
    };

    //apply to a job
    const applyToJob = async (jobId, ) => {
        try {
            const res = await axiosInstance.put(`/api/v1/jobs/apply/${jobId}`, );   
            toast.success("Applied to job successfully");
            getJobs();
            //update jobs state
        } catch (error) {
            console.error("Error applying to job", error);
            toast.error(error.response?.data?.message || "Error applying to job");
        }   
    };
    //delete a job
    const deleteJob = async (jobId) => {
        try {

            const res = await axiosInstance.delete(`/api/v1/jobs/${jobId}`);
            setJobs((prevJobs) => prevJobs.filter((job) => job._id !== jobId));
            setUserJobs((prevJobs) => prevJobs.filter((job) => job._id !== jobId));

            toast.success("Job deleted successfully");
        } catch (error) {
            console.error("Error deleting job", error);
            toast.error(error.response?.data?.message || "Error deleting job");
        }   
    };

    useEffect (() => {
        getJobs();
        // searchJobs("","","test data 2");
    },[]);

    useEffect(() => {   
        if(userProfile && userProfile._id){
            getUserJobs (userProfile._id);
        }   
    }, [userProfile]);
    //console.log("search Jobs:", jobs);
    

    return (
        <JobsContext.Provider value={{jobs,  loading,  userJobs, createJob,  searchJobs, getJobById, likeJob, applyToJob, deleteJob}}>
            {children}
        </JobsContext.Provider>
    );      
    
}

export const useJobsContext =() => {
    return useContext(JobsContext);
}
